<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">mixins</span>: [MPublisher],
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">value</span>: <span class="hljs-literal">undefined</span>,
        };
    },
    <span class="hljs-attr">publish</span>: {
        <span class="hljs-attr">someTopic</span>: <span class="hljs-string">'value'</span>,
    },
    <span class="hljs-attr">methods</span>: {
        onClick() {
            <span class="hljs-keyword">this</span>.value = <span class="hljs-keyword">this</span>.value === <span class="hljs-string">'red'</span> ? <span class="hljs-string">'blue'</span> : <span class="hljs-string">'red'</span>;
        },
    },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></code></pre>
